	import investorItem from '@/components/investor-item/investor-item.vue'
	import { queryMoreActivityUserPage } from '@/common/vmeitime-http/index.js'
    export default {
		components:{
			investorItem
		},
        data() {
            return {
				loadMoreText: "加载中...",
				showLoadMore: false,

				list:[],
				pages:0,
				params:{
					id:"e73df58706f03b36522b6e27a3da66fd",
					pageSize:15,
					pageNum:1,
				}
            }
        },
		onLoad(options) {
			if(options.id){
				this.params.id = options.id
			}
			this.queryMoreActivityUserPage()
		},
		onPullDownRefresh() {
			console.log('refresh');
			this.refresh()
			setTimeout(function () {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onReachBottom(){
			console.log('next');
			this.scrolltolower()
		},
        methods: {
			refresh(){
				this.list=[]
				this.params = {
					...this.params,
					pageSize:15,
					pageNum:1
				}
				this.queryMoreActivityUserPage()
			},
			async queryMoreActivityUserPage(){
				let res = await queryMoreActivityUserPage({
					...this.params
				})
				this.showLoadMore = false
				if(res.data.flag){
					this.pages = res.data.data.pages
					this.list = this.list.concat(res.data.data.list)
				}
			},
			// 分页
			scrolltolower() {
				if(this.params.pageNum >= this.pages || this.showLoadMore) return
				this.showLoadMore = true;
				this.params.pageNum++
				this.queryMoreActivityUserPage()
			}
        }
    }
